
import { defineComponent, reactive, ref } from 'vue';
import { Breadcrumb } from '@/assets/fixtures/interfaces';
import { fetchIssues } from '@/requests';
import colors from '@/assets/fixtures/colors';
import host from '@/assets/fixtures/url';
import iconSize from '@/assets/fixtures/iconSize';

export default defineComponent({
  name: 'IssueCard',
  async setup() {
    const issueKey = 'SBLRLP-1763';
    const issues = await fetchIssues();

    const issue = reactive(issues.find((item: any) => item.key === issueKey));
    const project: Breadcrumb = reactive({
      title: issue?.fields?.project?.name,
      href: `${host}/browse/${issue?.fields?.project?.key}`,
      disabled: false,
    });

    const task: Breadcrumb = reactive({
      title: issue?.key,
      href: `${host}/browse/${issue?.key}`,
      disabled: false,
    });

    const breadcrumbs: Array<Breadcrumb> = reactive([project, task]);
    const projectIcon: string = ref(issue?.fields?.project?.avatarUrls[iconSize.large]);
    const summary = ref(issue?.fields?.summary);
    const { subtasks } = reactive(issue?.fields);
    const donePercent = ref(((7 / subtasks.length) * 100).toFixed(0));
    const issuesList = reactive(issues.map((issueItem) => issueItem.key));

    return {
      issue,
      breadcrumbs,
      colors,
      projectIcon,
      summary,
      subtasks,
      donePercent,
      issuesList,
    };
  },
});
