import axios from 'axios';

class API {
  #config = {
    withCredentials: true,
    data: {},
    headers: {
      Accept: '*/*',
      'Content-Type': 'application/json',
    },
  }

  async get(endpoint, config = this.#config) {
    return await axios.get(endpoint, config);
  }

  async post(endpoint, body, config = this.#config) {
    return await axios.post(endpoint, body, config);
  }

  async put(endpoint, body, config = this.#config) {
    return await axios.put(endpoint, body, config);
  }

  async delete(endpoint, body = null, config = this.#config) {
    if (body) {
      return await axios.delete(endpoint, body, config);
    }
    return await axios.delete(endpoint, config);
  }
}

export default new API();
